import * as vscode from 'vscode';

// Core BMAD Types
export type BMadMode = 
    | 'documentation'
    | 'fullDevelopment'
    | 'continueProject'
    | 'taskOverview'
    | 'debugMode'
    | 'continuousExecution'
    | 'featureGapAnalysis'
    | 'githubIntegration';

export type ProjectType = 
    | 'react-frontend'
    | 'vue-frontend'
    | 'angular-frontend'
    | 'nodejs-api'
    | 'python-api'
    | 'rust-api'
    | 'fullstack-web'
    | 'mobile-app'
    | 'desktop-app'
    | 'library'
    | 'unknown';

export type ComplexityLevel = 'simple' | 'moderate' | 'complex' | 'enterprise';

export type AgentId = 'john' | 'fred' | 'jane' | 'sage' | 'alex' | 'tyler' | 'taylor' | 'sarah' | 'bob' | 'dakota';

// Project Analysis Interface
export interface ProjectAnalysis {
    type: ProjectType;
    framework: string;
    language: string;
    complexity: ComplexityLevel;
    hasDatabase: boolean;
    hasAuthentication: boolean;
    hasFrontend: boolean;
    hasBackend: boolean;
    hasDocker: boolean;
    hasKubernetes: boolean;
    hasTests: boolean;
    hasCICD: boolean;
    dependencies: string[];
    devDependencies: string[];
    recommendedMode: BMadMode;
    recommendedAgents: AgentId[];
    confidence: number; // 0-1 confidence score
}

// Agent Configuration
export interface AgentConfig {
    id: AgentId;
    name: string;
    title: string;
    description: string;
    persona: string;
    tasks: string[];
    templates?: string[];
    checklists?: string[];
    specializations: string[];
}

// Agent Selection and Recommendations
export interface AgentRecommendation {
    agent: AgentConfig;
    reason: string;
    confidence: number;
    required: boolean;
}

export interface AgentSelection {
    required: AgentId[];
    recommended: AgentId[];
    rationale: string;
}

// BMAD Configuration
export interface BMadConfiguration {
    autoInitialize: boolean;
    defaultMode: BMadMode;
    enableRealTimeMonitoring: boolean;
    autoActivateRecommendedAgents: boolean;
    debugModeThreshold: number;
    progressNotifications: boolean;
    intelligentRecommendations: boolean;
    statusBarIntegration: boolean;
}

// Workspace Analysis Results
export interface WorkspaceAnalysisResult {
    projectAnalysis: ProjectAnalysis;
    existingBmadSetup: boolean;
    bmadConfigPath?: string;
    needsInitialization: boolean;
    recommendations: {
        mode: BMadMode;
        agents: AgentRecommendation[];
        actions: string[];
    };
}

// Progress Tracking
export interface WorkflowProgress {
    mode: BMadMode;
    phase: string;
    progress: number; // 0-100
    currentAgent?: AgentId;
    estimatedTimeRemaining?: number;
    completedTasks: string[];
    remainingTasks: string[];
}

// Issue Detection
export interface ProjectIssue {
    severity: 'critical' | 'high' | 'medium' | 'low';
    category: 'security' | 'performance' | 'quality' | 'dependency' | 'configuration';
    message: string;
    file?: string;
    line?: number;
    suggestedAction?: string;
    canAutoFix: boolean;
}

// Mode Execution Context
export interface ModeExecutionContext {
    mode: BMadMode;
    workspaceFolder: vscode.WorkspaceFolder;
    projectAnalysis: ProjectAnalysis;
    selectedAgents: AgentId[];
    userPreferences: Partial<BMadConfiguration>;
    existingArtifacts: string[];
}

// File System Operations
export interface BMadFileStructure {
    bmadAgentPath: string;
    personasPath: string;
    tasksPath: string;
    templatesPath: string;
    checklistsPath: string;
    dataPath: string;
    configPath: string;
}

// Technology Stack Detection
export interface TechnologyStack {
    frontend: {
        framework?: string;
        version?: string;
        buildTool?: string;
        packageManager?: string;
    };
    backend: {
        language?: string;
        framework?: string;
        version?: string;
        runtime?: string;
    };
    database: {
        type?: string;
        version?: string;
        orm?: string;
    };
    infrastructure: {
        containerization?: string;
        orchestration?: string;
        cloudProvider?: string;
        cicd?: string;
    };
    testing: {
        unitTesting?: string;
        e2eTesting?: string;
        coverage?: string;
    };
}

// Command Execution Results
export interface CommandResult {
    success: boolean;
    message: string;
    data?: any;
    errors?: string[];
    warnings?: string[];
}

// Workspace Monitoring Events
export interface WorkspaceEvent {
    type: 'fileChange' | 'diagnosticChange' | 'configChange' | 'dependencyChange';
    timestamp: Date;
    details: any;
    triggeredActions: string[];
}

// Agent Handoff Context
export interface AgentHandoffContext {
    fromAgent: AgentId;
    toAgent: AgentId;
    currentTask: string;
    completedWork: string[];
    pendingWork: string[];
    contextData: any;
    handoffReason: string;
}

// Quick Pick Items for VS Code UI
export interface BMadQuickPickItem extends vscode.QuickPickItem {
    id: string;
    data?: any;
}

// Status Bar Information
export interface StatusBarInfo {
    mode?: BMadMode;
    activeAgents: AgentId[];
    progress?: WorkflowProgress;
    issues?: ProjectIssue[];
    lastUpdate: Date;
}

// Error Types
export class BMadError extends Error {
    constructor(
        message: string,
        public code: string,
        public category: 'initialization' | 'analysis' | 'execution' | 'configuration' = 'execution'
    ) {
        super(message);
        this.name = 'BMadError';
    }
}

// Event Emitter Types
export interface BMadEvents {
    'modeActivated': { mode: BMadMode; agents: AgentId[] };
    'agentActivated': { agent: AgentId; context: any };
    'progressUpdated': WorkflowProgress;
    'issueDetected': ProjectIssue[];
    'workspaceAnalyzed': WorkspaceAnalysisResult;
    'configurationChanged': BMadConfiguration;
}
